* {
  margin: 0;
  padding: 0
}

.main {
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 100%
}

.navbar {
  background-color: #4472c4;
  color: #fff;
  justify-content: center
}

.navbar,.navigators {
  align-items: center;
  display: flex;
  height: 10vh;
  width: 100%
}

.navigator-btn {
  background-color: #4472c4;
  border: none;
  border-radius: 2px;
  color: #fff;
  height: 50%;
  margin: 0 4%;
  padding: 0 2%
}

.Retrieve {
  border: 1px solid #000;
  height: 75vh;
  width: 92%
}

.find {
  height: 8%;
  width: 50%
}

.find-form {
  display: flex;
  height: 100%
}

.search-aadhar {
  margin-right: 2%;
  text-align: center
}

.search-aadhar:focus {
  outline: none
}

.find-btn {
  background-color: #4472c4;
  border: none;
  color: #fff;
  width: 20%
}

.result {
  margin: 2% 3% 0;
  width: 92%
}

.AddNewPerson {
  border: 1px solid #000;
  height: 75vh;
  position: relative;
  width: 92%
}

.page-title {
  align-items: center;
  border-bottom: 1px solid grey;
  border-right: 1px solid grey;
  display: flex;
  font-weight: bolder;
  height: 3rem;
  justify-content: center;
  margin-bottom: 2rem;
  width: 20%
}

table {
  margin: 0 3%;
  width: 94%
}

tr {
  height: 35px
}

thead {
  background-color: #4472c4;
  color: #fff
}

td {
  text-align: center
}

.Add-btn {
  background-color: #4472c4;
  border: none;
  border-radius: 2px;
  bottom: 1%;
  color: #fff;
  height: 8%;
  position: absolute;
  right: 1%;
  width: 10%
}

.row-btns {
  background-color: #fff;
  border: none;
  color: blue;
  text-decoration: underline
}

form {
  display: flex;
  justify-content: space-between;
  margin: 0 3%;
  width: 94%
}

.form-container {
  background-color: #4472c4;
  border: 1px solid grey;
  color: #fff;
  height: 3rem;
  margin: 5px 3% 0;
  width: 94%
}

form>input {
  width: 100%
}
